.suggestionProduct {
  background-color: white;
  padding: 10px;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.1);
  &_header {
    width: 100%;
    margin-bottom: 10px;
    border-bottom: 1px solid rgb(219, 218, 218);
    h3 {
      padding: 20px 15px;
      margin: 0;
      font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    }
  }
  &_content{
   display: flex;
   flex-wrap: wrap;
  }
  &_seeMore{
    padding: 20px;
    text-align: center;
    .seeMore{
      cursor: pointer;
      margin: auto;
      width: 120px;
      height: 40px;
      color: white;
      display: flex;
      align-items: center;
      background-color: #191919;
      font-family: Arial, Helvetica, sans-serif;
      .text{
        width: 100%;
        text-align: center !important;
      }
    }
   
  }
}
